<template>
    <el-breadcrumb class="app-breadcrumb" separator=">">
        <transition-group name="breadcrumb">
            <el-breadcrumb-item v-for="(item)  in $route.matched" :key="item.path">
                <template>
                    <span v-if="item.meta.hidden" class="no-redirect">{{item.meta.title}}</span>
                    <span>{{item.meta.title || item.meta.name}}</span>
                    <!-- <router-link class="home-link" v-else-if="item.meta.title == '首页'" :to="`${item.redirect || item.path,$route.params | routeLink}`">{{item.meta.title}}</router-link>
                    <router-link v-else :to="`${item.redirect || item.path,$route.params | routeLink}`">{{item.meta.title}}</router-link> -->
                </template>
            </el-breadcrumb-item>
        </transition-group>
    </el-breadcrumb>
</template>
<script>
export default {
    name: 'BreadcrumbBar',
    data () {
        return {
            levelList: []
        };
    },

    created () {
        this.getBreadcrumb();
    },

    watch: {
        $route () {
            this.getBreadcrumb();
        }
    },

    methods: {
        getBreadcrumb () {
            this.levelList = this.$route.matched;
        }
    }
};
</script>
<style lang="less" scoped>
   .app-breadcrumb {
       padding: 0 20px;
       font-size: 14px;
       line-height: 45px;
       border-bottom: 1px solid #dedede;
       .no-redirect {
           color: #3d9eff;
           cursor: text;
       }
   }
   .home-link{
       color: #334495;
   }
</style>



