<template>
  <el-container class="layout-container">
    <el-header height="auto">
      <header-top/>
    </el-header>
    <el-container>
      <el-aside width="auto" class="rh-bg-white">
        <sidebar/>
      </el-aside>
      <el-main>
        <!-- <breadcrumb-bar/> -->
        <div class="layout-view">
          <transition name="fade" mode="out-in">
            <!-- <keep-alive> -->
              <!-- <router-view :key="key"></router-view> -->
              <router-view></router-view>
            <!-- </keep-alive> -->
          </transition>
        </div>
      </el-main>
    </el-container>
  </el-container>
</template>
<script type="text/ecmascript-6">
import { mapGetters } from "vuex";
import HeaderTop from "@/views/layout/Header";
import Sidebar from "@/views/layout/Sidebar";
import BreadcrumbBar from "@/views/layout/BreadcrumbBar";
export default {
  name: "Main",
  mounted() {
    //移除登录后的动画
    const loading = document.getElementsByClassName("lds-css");
    if (loading && loading.length > 0) loading[0].remove();
  },
  computed: {
    ...mapGetters(["sideBarMenus"]),
    //菜单
    sideMenus() {
      const { sideBarMenus } = this;
      return sideBarMenus.children;
    },
    key() {
      // return this.$route.path + Date.parse(new Date());
      return this.$route.path;
    },
    // ...mapGetters(["isCollapseSidebar"])
  },

  components: {
    BreadcrumbBar,
    HeaderTop,
    Sidebar
  }
};
</script>
<style lang="less">
.layout-container {
  height: 100%;
  box-sizing: border-box;

}

.layout-view {
    width: 100%;
    // padding: 20px 20px 0 20px;
    // margin-bottom: 20px;
    box-sizing: border-box;
    .content_box{
      box-sizing: border-box;
      // margin: 20px 20px 20px 20px;
       margin: 20px 20px 0 20px;
    }
    .el-dialog {
      background:rgba(247,248,250,1);
      border-radius:0px 0px 2px 2px;
      /deep/ .el-dialog__header{
        height:40px;
        line-height: 40px;
        text-align: center;
        padding: 0 ;
        background:rgba(54,123,246,1);
        border-radius:2px 2px 0px 0px;
        /deep/ .dialog-title{
          font-size:16px;
          font-weight:500;
          color:rgba(255,255,255,1);
        }
        /deep/ .el-dialog__headerbtn{
          top: 14px;
        }
      }
      /deep/ .el-dialog__body{
        padding: 15px 20px 20px 20px;
        font-size:14px;
        /deep/ .app-table-utils{
          margin-bottom: 14px;
        }
      }
      /deep/ .el-button{
        width: 120px;
        height: 32px;
        font-family: 'PingFang-SC-Medium';
        font-weight:400;
        color:rgba(255,255,255,1);
        background:rgba(54,123,246,1);
        border-radius:2px;
        border: rgba(54,123,246,1);
      }
      /deep/ .resetForm{
        background:rgba(244,244,244,1);
        color:rgba(51,51,51,1);
        border:1px solid rgba(221,221,221,1);
      }

      /deep/ .el-button:hover{
        opacity: 0.9;
      }
    }
}


// 表格中的直线分割
.table-line{
  height:1px;
  background:rgba(239,240,242,1);
}

</style>




