<template>
    <div class="header-top">
        <div class="header-left">
          <img class="header-logo" :src="require(`@/assets/imgs/logo.png`)" alt="logo">
        </div>
        <div class="header-center">
            <top-menus></top-menus>
        </div>
        <div class="header-right">
            <div class="box">
                <div class="user-menu">
                    <img class="user-avatar" :src="require(`@/assets/imgs/default-avatar.png`)" alt="user">
                    <el-dropdown @command="handleCommand"  @visible-change="dropdownClick($event)" trigger="click">

                        <div class="user-content">
                          <h4 :class="{'active':isClick}"  >
                            你好！admin
                            <i v-if="!isClick" class="bottom-icon"></i>
                            <i v-if="isClick" class="top-icon"></i>

                          </h4>
                        </div>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="logout">
                              <i class="log-out-icon" :style="{backgroundImage:`url(${require(`@/assets/imgs/log-out.png`)})`}"></i>
                                退出系统
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </div>
        </div>
    </div>
</template>

<script  type="text/ecmascript-6">
import TopMenus from './TopMenus.vue';
export default {
    name: 'Header',
    components: {
        TopMenus,
    },
    data () {
        return {
          isClick:false,
          userInfo: JSON.parse(window.localStorage.getItem(`currentUserSessionData`)) || null,//登录用户信息
        };
    },
    methods: {
        //下拉回调
        handleCommand (command) {
            switch (command) {
            case 'logout': //退出系统
                this.logout(); break;
            }
        },

        dropdownClick(value){
          console.log(value);
          this.isClick = value;
        },

        //退出系统
        logout () {
            this.$confirm('您确定要退出系统吗?', '提示', {
                cancelButtonText: '取消',
                confirmButtonText: '确定',
                type: 'warning',
                center: true,
                customClass: 'bg-warning'
            }).then(async () => {
                this.$router.push({  path: '/',name: 'Login',});
                this.$store.commit('CLEAR_USER_TOKEN', token);
            }).catch(() => {});
        }
    }

};
</script>
<style lang="less" scoped>
    @header-height:66px;

    .header-top{
      display: flex;
      height: @header-height;
      background:rgba(255,255,255,1);
      // box-shadow:0px 0px 4px 0px rgba(23,23,38,0.16);
      border-bottom: 1px solid rgba(229,229,229,1);
      font-size:16px;
      font-weight:500;
      color:rgba(89,89,89,1);
      .header-left{
          display: flex;
          align-items: center;
          justify-content: center;
          width: 350px;
      }
      .header-center{
        display: none;
        padding: 15px 20px;
        font-size: 14px;
      }


      .header-right{
          flex: 1;
          .box{
              display: flex;
              align-items: center;
              justify-content: flex-end;
              height: @header-height;
              padding-right: 32px;
              .user-menu{
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  min-width: 166px;
                  .user-avatar{
                    width: 36px;
                    display: inline-block;
                    vertical-align: middle;
                  }
                  .el-dropdown{
                        vertical-align: middle;
                        color: #fff;
                        font-size: 14px;
                        cursor: pointer;
                        .user-content{
                          display: inline-block;
                          vertical-align: middle;
                          margin-left: 9px;
                          font-size:14px;
                          font-weight:500;
                          color:rgba(136,136,136,1);
                          .bottom-icon{
                            display: inline-block;
                            margin: 0 0 -5px 5px;
                            width:0;
                            height:0;
                            border:6px solid transparent;
                            border-top-color:rgba(153,156,158,1);
                          }
                          .top-icon{
                            display: inline-block;
                            margin: 0 0 1px 5px;
                            width:0;
                            height:0;
                            border:6px solid transparent;
                            border-bottom-color:rgba(54,123,246,1);
                          }
                        }

                    }
              }
          }
      }
    }

    .el-dropdown-menu,.el-popper,.el-dropdown-menu--mini{
      box-sizing: border-box;
      position: absolute !important;
      top: 45px !important;
      right: 22px !important;
      width: 150px;
      height: 45px;
      /deep/ .el-dropdown-menu__item{
        display: flex;
        justify-content: center;
        align-items: center;
        width:130px;
        height:45px;
        background:rgba(255,255,255,1);
        border-radius:2px;
        .log-out-icon{
          display: inline-block;
          width: 22px;
          height: 22px;
          margin-right: 10px;
        }
      }
      /deep/ .popper__arrow{
        display: none;
      }



    }

    .active{
      color: rgba(54,123,246,1);
    }


</style>



