<template>
    <div>
        <ul class="menu-list" v-if="routeOptions && routeOptions.length > 0">
            <li v-for="(menu,index) in routeOptions" :key="index">
                <keep-alive>
                    <a
                      v-if="menu.meta"
                      class="menu-item"
                      :class="{active:$route.path.substring(0,menu.path.length).indexOf(menu.path) != -1}"
                      @click="goToPage(menu)">
                      <p>
                        {{menu.meta.title}}
                        <i></i>
                      </p>
                    </a>
                </keep-alive>
            </li>
        </ul>
    </div>
</template>
<script type="text/ecmascript-6">
import { mapGetters, mapActions } from 'vuex';
export default {
    name: 'TopMenus',
    watch: {
        $route (val) {
            this.routeOptions.forEach((item, index) => {
                if (val.matched[0].path === item.path) {
                    this.updateSideBarMenus(item);
                }
            });
        }
    },

    created () {
        this._initSideBar();
        console.log(this.routeOptions)
    },

    methods: {

        /**
		 * 初始化
		 * */
        _initSideBar () {
            const { routeOptions } = this;
            if (routeOptions && routeOptions.length > 0) {
                for (let menu of routeOptions) {
                    if (this.$route.path.substring(0, menu.path.length).indexOf(menu.path) != -1) {
                        //初始化菜单
                        this.updateSideBarMenus(menu);
                    }
                }
            }
        },

        /**
		 * 跳转到对应的页面
		 * */
        goToPage (route) {
            //监控台
            if (!route.children) {
                this.$router.replace({
                    path: '/404',
                    query: { redirect: this.$router.currentRoute.fullPath }
                });
            } else {
                 this.$router.push(`${route.path}/${route.children[0].path}`);
                // console.log(route.children[0])
            }
        },

        ...mapActions([
            'updateSideBarMenus'
        ])
    },
    computed: {
        ...mapGetters([
            'routeOptions'
        ]),

    }
};
</script>
<style lang="less" scoped>
    .menu-list{
        display: flex;
        text-align: center;
        font-size:16px;
        font-weight:500;
        color:rgba(89,89,89,1);
        .menu-item{
            cursor: pointer;
            color: #000;
            display: inline-block;
            padding: 10px 25px;
            p{
              position: relative;
              white-space:nowrap;
            }
        }
        & .active{
          color:rgba(226,35,26,1);
          i{
            position: absolute;
            top:34px;
            left: 45%;
            width:0;
            height:0;
            border:3.5px solid transparent;
            border-bottom-color:red;
          }
        }
    }
</style>



