<template>
    <div v-if="sideBarMenus" class="sidebar-menu">
        <el-menu
          :unique-opened="true"
          :default-active="activeIndex"
          background-color="#292D30"
          text-color="#FFFFFF"
          active-text-color="#E2231A">
            <!-- 一级菜单 -->
          <template v-for="item in sideMenus" >
            <el-submenu  v-if="item.children && item.children.length" :index="item.path" :key="item.path"  :class="{'hide': !item.meta.requireAuth}">
              <template slot="title" v-if="item.meta.title && item.meta.requireAuth"><i class="left-icon" :style="{backgroundImage:`url(${require(`@/assets/imgs/left-icon.png`)})`}"></i><span class="span_title">{{item.meta.title}}</span></template>
              <!-- 二级菜单 -->
              <template v-for="itemChild in item.children">
                <el-menu-item  :index="itemChild.path" :key="itemChild.path" :class="{activeBg:`${sideBarMenus.path}/${item.path}/${itemChild.path}`.indexOf($route.path) != -1}" v-if="itemChild.meta.title && itemChild.meta.requireAuth">
                  <router-link :to="`${sideBarMenus.path}/${item.path}/${itemChild.path}`">
                    <span  :class="{active:`${sideBarMenus.path}/${item.path}/${itemChild.path}`.indexOf($route.path) != -1}">{{itemChild.meta.title}}</span>
                  </router-link>
              </el-menu-item>
              </template>
            </el-submenu>

            <el-menu-item  v-else :index="item.path" :key="item.path" :class="{'hide': !item.meta.requireAuth, activeBg:`${sideBarMenus.path}/${item.path}`.indexOf($route.path) != -1}" class="menu_title">
              <router-link :to="`${sideBarMenus.path}/${item.path}`">
                <template  v-if="item.meta.title && item.meta.requireAuth" :class="{active:`${sideBarMenus.path}/${item.path}`.indexOf($route.path) != -1}">
                  <i class="left-icon icon-Title" :style="{backgroundImage:`url(${require(`@/assets/imgs/left-icon.png`)})`}"></i>
                  <span>{{item.meta.title}}</span>
                </template>
              </router-link>
            </el-menu-item>

          </template>
        </el-menu>
    </div>
</template>
<script type="text/ecmascript-6">
import { mapGetters } from 'vuex';
export default {
    name: 'Sidebar',
    data(){
     return {
        activeIndex:'',
        sideMenusData:null,
        parentItem:null,
     }
    },
    computed: {
        ...mapGetters([
            'sideBarMenus'
        ]),

        //菜单
        sideMenus () {
          const { sideBarMenus } = this;
          return sideBarMenus.children;
        },
    },
    mounted(){
      this.parentItem = this.$route.path.split(/\//)[1];
      this.activeIndex = this.$route.name;
    },
};
</script>
<style lang="less" scoped>
.sidebar-menu{
    width: 200px;
    background:rgba(38,42,51,1);
    height: 100%;
    font-size:14px !important;
    box-sizing: border-box;
    .el-menu{
      box-sizing: border-box;
      width: 200px;
      border: 0;
    }
    .el-submenu {
       box-sizing: border-box;
       /deep/ .el-submenu__title{
          height: 44px !important;
          line-height: 44px;
          letter-spacing: 2px;
          margin: 0 0 0 1px;
       }
    }


    /deep/ .el-menu-item{
      height: 44px !important;
      line-height: 44px;
      box-sizing: border-box;
      padding-left: 37px !important;
      a{
        display: inline-block;
        height: 44px !important;
        text-decoration: none;
        width: 100%;
        color: #FFFFFF;
      }
    }
    .el-menu-item.is-active {
      height: 44px !important;
      background: rgba(54,123,246,1) !important;
    }
    .active{
      color: #FFFFFF;
    }
    .activeBg{
      background: rgba(54,123,246,1) !important;
    }
    .left-icon{
      display: inline-block;
      width: 10px;
      height: 13px;
      margin-right: 6px;
    }

    .icon-Title{
      margin-right: 3px !important;
    }

    // .span_title{
    //   display: inline-block;
    //   margin: 0 0 0 1px;
    //   // line-height: 44px;
    //   // height: 44px !important;
    // }


    .hide{
      display: none !important;
    }

    .menu_title{
      padding: 0 21px !important;
    }
}

</style>



